body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{background-color:#121616;height:auto}
.home-box{background:#192325;background:linear-gradient(to right, #0d1112 0%, #1d3130 45%, #136952 85%, #019664 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr="#0d1112", endColorstr="#198c71",GradientType=1 );height:100vh;display:flex;flex-direction:column;justify-content:space-between}.home-nav{display:flex;flex-direction:column;align-items:center;justify-content:center;width:100%}@media(min-width: 812px){.home-nav{flex-direction:row}}
header{margin:0 auto;padding:10px 3vw;display:inline-flex;justify-content:space-between}header .logo-svg{width:50px;height:50px;fill-opacity:.7}header .logo-svg:hover{fill-opacity:.9}
.nav-box{background-color:red;width:100px;height:auto}.menu_button{background-color:purple;width:30px;height:30px}
.nav-item{background-color:#0084ff;width:100px;height:20px}
.home_link{display:flex;justify-content:center;align-items:center;width:110px;height:80px;font-size:150%;margin:0 20px}.home_link span{color:#019664;height:100%}.home_link a{color:#18584a;text-decoration:none;font-family:"Rubik";font-weight:bold;height:100%;padding:2.5px 1px 0 1px;text-transform:uppercase}.home_link:hover span{color:#136952}.home_link:hover a{color:#10c286}@media(min-width: 812px){.home_link:hover{border:none}.home_link:hover a{border-color:#10c286;border-bottom:solid 3px}}

.content-type-box{height:600px}
.about-box{height:400px}
